#banner2  {
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    height: 300px;
    width: 100%;
    background: url('../img/banner2.jpg') no-repeat center center fixed;
}
#banner2:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    background: url('../svg/chevron_overlay_half.svg') no-repeat center center;
    -webkit-background-size: cover!important;
    -moz-background-size: cover!important;
    -o-background-size: cover!important;
    background-size: cover!important;
    z-index: -1
}
@media only screen and (max-width:749px) {
    .jarallax {
        background-attachment: scroll;
        background-size: 100% 100%;
    }
}
.img-fill {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden
}

.img-fill img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%
}

.schedule-time {
    border-right: 1px solid #999;
    padding-right: 24px;
    margin-right: 8px;
}

@media (min-width: 1200px) {
    .schedule-container {
        max-width:1160px
    }
}

@media (min-width: 1700px) {
    .schedule-container {
        max-width:1660px
    }
}

.schedule-container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    width: 100%
}

.bold-spans > li > span {
    font-weight: bold;
}

.light-background {
    background-color: #272727;
}